/* overpass-300 - latin-ext_latin */
@font-face {
  font-family: "Overpass";
  font-style: normal;
  font-weight: 300;
  src: url("../fonts/overpass-v5-latin-ext_latin-300.eot"); /* IE9 Compat Modes */
  src: local(""),
    url("../fonts/overpass-v5-latin-ext_latin-300.eot?#iefix")
      format("embedded-opentype"),
    /* IE6-IE8 */ url("../fonts/overpass-v5-latin-ext_latin-300.woff2")
      format("woff2"),
    /* Super Modern Browsers */
      url("../fonts/overpass-v5-latin-ext_latin-300.woff") format("woff"),
    /* Modern Browsers */ url("../fonts/overpass-v5-latin-ext_latin-300.ttf")
      format("truetype"),
    /* Safari, Android, iOS */
      url("../fonts/overpass-v5-latin-ext_latin-300.svg#Overpass") format("svg"); /* Legacy iOS */
}
